-data:active {\n  background-color: blue;\n}", "",{"version":3,"sources":["webpack://./src/styles/style.scss"],"names":[],"mappings":"AAoCA;EACE,eAAA;EACA,yBAxBmB;EAyBnB,+JAAA;AAlCF;;AAuCA;;;EAGE,sBAAA;EACA,SAAA;EACA,UAAA;AApCF;;AAwCA;;;;EAIE,aAAA;AArCF;;AAwCA;EAxCE,aAAA;EACA,sBAwCmB;EAvCnB,8BAuC2B;EAtC3B,mBAsC0C;EAC1C,YAAA;EACA,YAAA;EACA,sCArDW;AAmBb;;AAqCA;EACE,oBAAA;EACA,kBAAA;EACA,qBAAA;AAlCF;AAoCE;EACE,oBAAA;EACA,sCA9DW;EA+DX,yBA7DiB;EA8DjB,sCAAA;EACA,qBAAA;EAEA,qBAAA;AAnCJ;AAsCE;EAnDA,+KAAA;EAqDE,4BArEmB;EAsEnB,uCAAA;AApCJ;;AAwCA;EACE,kBAAA;EACA,gBAAA;EACA,aAAA;EACA,aAAA;EACA,eAAA;AArCF;;AAwCA;EACE,aAAA;EACA,iBAAA;EACA,iBAAA;AArCF;AAuCE;EACE,iBAAA;AArCJ;AAwCE;EACE,kBAAA;EACA,gBAAA;AAtCJ;AAuCI;EACE,4CAAA;EACA,cAAA;EACA,qBAAA;AArCN;;AA0CA;EACE,kBAAA;AAvCF;;AA0CA;EA9FE,+CAAA;EAgGA,kCAAA;EACA,4BAAA;EACA,6BAAA;EACA,iBAAA;EACA,uCAlHY;EAmHZ,eAAA;AAvCF;AAyCE;EACE,sBAAA;AAvCJ","sourcesContent":["@import url(./fonts.scss);\n\n$pallete: (\n  ocean-green: hsla(159, 62%, 47%, 1),\n  black: hsla(210, 9%, 13%, 1),\n  space-grey: hsla(192, 4%, 23%, 1),\n  dark-green: hsla(148, 55%, 30%, 1),\n  white: hsla(0, 0%, 96%, 1),\n);\n\n// App colors\n$bg-primary: map-get($pallete, black);\n$bg-secondary: map-get($pallete, space-grey);\n$bg-tertiary: map-get($pallete, ocean-green);\n$text-color-primary: map-get($pallete, white);\n$text-color-secondary: map-get($pallete, ocean-green);\n$shadow-black: rgb(30, 33, 36);\n$shadow-green: rgb(34, 119, 74);\n\n@mixin init-flex($direction, $justify: flex-start, $align: flex-start) {\n  display: flex;\n  flex-direction: $direction;\n  justify-content: $justify;\n  align-items: $align;\n}\n\n@mixin card-shadow($color) {\n  box-shadow: 0 8px 32px 0 rgba($color, 0.37);\n}\n\n@mixin under-glow($color) {\n  box-shadow: rgba($color, 0.4) 0px 5px, rgba($color, 0.3) 0px 10px,\n    rgba($color, 0.2) 0px 15px, rgba($color, 0.1) 0px 20px,\n    rgba($color, 0.05) 0px 25px;\n}\n\n:root {\n  font-size: 16px;\n  color: $text-color-primary;\n  font-family: \"OpenSans\", system-ui, -apple-system, BlinkMacSystemFont,\n    \"Segoe UI\", Roboto, Oxygen, Ubuntu, Cantarell, \"Open Sans\", \"Helvetica Neue\",\n    sans-serif;\n}\n\n*,\n*::before,\n*::after {\n  box-sizing: border-box;\n  margin: 0;\n  padding: 0;\n  // border: 1px solid red;\n}\n\ninput:focus,\nselect:focus,\ntextarea:focus,\nbutton:focus {\n  outline: none;\n}\n\nbody {\n  @include init-flex(column, space-between, center);\n  height: 100%;\n  width: 100vw;\n  background-color: $bg-primary;\n}\n\n.search-bar {\n  align-self: flex-end;\n  margin-top: 0.5rem;\n  margin-right: 0.25rem;\n\n  &__input {\n    padding-left: 0.5rem;\n    background-color: $bg-secondary;\n    color: $text-color-primary;\n    border: 1px solid $bg-secondary;\n    border-radius: 0.7rem;\n\n    transition: all 200ms;\n  }\n\n  &__input:focus {\n    @include under-glow($shadow-green);\n    color: $text-color-secondary;\n    border: 1px solid $bg-tertiary;\n  }\n}\n\n.main {\n  position: relative;\n  margin-top: 5rem;\n  bottom: 10rem;\n  display: flex;\n  padding: 0 1rem;\n}\n\n.report {\n  display: flex;\n  flex-flow: column;\n  font-size: 1.5rem;\n\n  &__headline {\n    line-height: 4rem;\n  }\n\n  &__suggestions {\n    position: relative;\n    list-style: none;\n    &--active {\n      border-left: 2px solid $bg-tertiary;\n      margin: 1rem 0;\n      padding-left: 0.25rem;\n    }\n  }\n}\n\n.temperature {\n  text-align: center;\n}\n\n.weather-data {\n  @include card-shadow($shadow-black);\n  padding: 0.75rem 5rem 0.25rem 5rem;\n  border-top-left-radius: 15px;\n  border-top-right-radius: 15px;\n  font-size: 1.7rem;\n  background-color: $bg-tertiary;\n  cursor: pointer;\n\n  &:active {\n    background-color: blue;\n  }\n}\n"],"sourceRoot":""}]);
